"use client";

import React, { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import {
  Bath,
  BedDouble,
  MapPin,
  Ruler,
  Search,
} from "lucide-react";

import FreeAddressSearch from "./FreeAddressSearch";
import { Button } from "@/components/ui/button";
import FilterSection from "./FilterSection";
// üí∞ Indian price formatter (UI only)
const formatPrice = (price) => {
  if (!price || isNaN(price)) return "";

  if (price >= 10000000) {
    return `‚Çπ${(price / 10000000).toFixed(2)} Cr`;
  } else if (price >= 100000) {
    return `‚Çπ${(price / 100000).toFixed(1)} Lakh`;
  }

  return `‚Çπ${Number(price).toLocaleString("en-IN")}`;
};

function Listing({
  listing = [],
  handleSearchClick,
  searchedAddress,
  setBathCount,
  setBedCount,
  setParkingCount,
  setHomeType,
  setCoordinates, // ‚úÖ comes from parent
}) {
  const [address, setAddress] = useState();
  console.log(listing)
  return (
    <div>

      {/* üîç Search Bar */}
      <div className="p-3 flex gap-6">
        <FreeAddressSearch
          selectedAddress={(v) => {
            searchedAddress && searchedAddress(v);
            setAddress(v);
          }}
          setCoordinates={setCoordinates}
        />

          <Button
  onClick={handleSearchClick}
  className="flex gap-2 bg-blue-600 hover:bg-blue-700 text-white"
>
  <Search className="h-4 w-4" />
  Search
</Button>

        {/* <Button className="flex gap-2" onClick={handleSearchClick}>
          <Search className="h-4 w-4  bg-blue-600 hover:bg-blue-700 text-white" />
          Search
        </Button> */}
      </div>

      {/* üéõ Filters */}
      <FilterSection
        setBathCount={setBathCount}
        setBedCount={setBedCount}
        setParkingCount={setParkingCount}
        setHomeType={setHomeType}
      />

      {/* üìä Result Count */}
      {address && (
        <div className="px-3 my-5">
          <h2 className="text-xl">
            Found{" "}
            <span className="font-bold">
              {listing?.length}
            </span>{" "}
            result{listing?.length > 1 ? "s" : ""} in{" "}
            <span className="font-bold text-blue-600">
              {address?.label || address}
            </span>
          </h2>
        </div>
      )}

      {/* üè† Listing Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 px-3">
        {listing?.length > 0 ? (
          listing.map((item) => (
            <Link
              key={item.id}
              href={'/view-listing/' + item.id}
              className="block"
            >
              <div className="p-3 hover:border hover:border-primary rounded-1g cursor-pointer">

                <img
  src={item?.listingImages?.[0]?.url}
  className="rounded-lg h-[180px] w-full object-cover"
/>
                </div>
                <div className="flex mt-2 flex-col gap-2">
                  <h2 className="font-bold text-xl text-blue-600">
  {formatPrice(item.price)}
</h2>
                  {/* <h2 className="font-bold text-xl text-blue-600">
                    ‚Çπ{item.price}
                  </h2> */}

                  <div className="flex gap-2 items-center text-sm text-gray-500">
                    <MapPin className="h-4 w-4" />
                    <span>{item.address}</span>{item.type}
                  </div>

                  <div className="flex gap-2 justify-between mt-2">
                    <span className="flex gap-2 items-center text-xs bg-slate-200 rounded-md px-2 py-1">
                      <BedDouble className="h-4 w-4" />
                      {item.bedroom}
                    </span>

                    <span className="flex gap-2 items-center text-xs bg-slate-200 rounded-md px-2 py-1">
                      <Bath className="h-4 w-4" />
                      {item.bathroom}
                    </span>

                    <span className="flex gap-2 items-center text-xs bg-slate-200 rounded-md px-2 py-1">
                      <Ruler className="h-4 w-4" />
                      {item.area}
                    </span>
                  </div>
                </div>

            </Link>
          ))
        ) : (
          [1, 2, 3, 4, 5, 6, 7, 8].map((_, index) => (
            <div
              key={index}
              className="h-[230px] w-full bg-slate-200 animate-pulse rounded-lg"
            />
          ))
        )}
      </div>
    </div>);
}
export default Listing;